      <div>请复制下方链接或点击右上角直接在手机浏览器内打开</div>
      <div>{{href}}</div>
    </div>
    <div v-show="!isWeixin">
      <canvas id="cvs" width="375" height="667"></canvas>
      <!-- bar -->
      <header class="header">
        <img @click="back" class="back" src="../assets/back-icon.png" alt="<" />
