        <div class="wrapper">
          <div class="main">
            <div class="inner">
              <div class="box">
                <!-- 圆圈 -->
                <div :style="{ background: `url('${avaterMap[info.type || 1]}')` }" class="avatar">
                </div>
                <!-- 证书标题 -->
                <img class="title" src="../assets/title.png" alt="区块链绿电溯源证书" />
                <!-- 电力流向 -->
                <div class="formTo">
                  <div>
