                  </div>
                </div>
                <!-- 绿电通证 -->
                <div class="infoWrapper">
                  <div class="greenPass">
                    <div class="label">绿电通证</div>
                    <div class="greenPassNumber">
                      <span class="number">{{info.miniId}}</span>
                      <span @click="showPopup" class="btn">查看完整通证</span>
                    </div>
                    <div>
                      <span class="label">绿电类型</span>
                      <span class="font">{{info.typeName}}</span>
                    </div>
                    <div>
                      <span class="label">调度哈希</span>
                      <span class="font">{{info.contractTxHashFormat}}</span>
                      <span style="float:right" @click="showPopup2" class="btn">查看完整通证</span>
                    </div>
                    <div>
                      <span class="label">通证生成时间</span>
                      <span class="font">{{info.certificateCreateTimeFormat}}</span>
                    </div>
                    <!-- <div>
                      <span class="label">运营商编号</span>
                      <span class="font">{{info.operatorId}}</span>
                    </div>
                    <div>
                      <span class="label">运营商名称</span>
                      <span class="font">{{info.operatorName}}</span>
                    </div> -->
                  </div>
                </div>
                <!-- 底部声明 -->
                <div class="bottomInfo">
                  <div>本溯源证书内数据皆源于绿电实际上链的记录</div>
                  <div>由区块链技术保证数据真实可靠，任何人或机构无法篡改</div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- 底部 -->
        <!-- <div class="bottom">
          <img src="../assets/icon-up.png" alt="up" class="upIcon">
          <div class="bottomDesc">上划查看溯源详情</div>
        </div> -->
        <page-second :info="info" />
      </div>
      
      <div id="qrcode"></div>
      <!-- 弹窗 -->
      <van-popup class="dialog" v-model="visible">{{info.id}}</van-popup>
      <van-popup class="dialog" v-model="visible2">{{info.contractTxHash}}</van-popup>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import PageSecond from '@/components/PageSecond.vue'
import jsBridge from '../utils/jsBridge'
import isWeixin from '../utils/isWeixin'
// import html2canvas from 'html2canvas'
import base64Str from '../components/base64'
import { parse } from 'querystring'
import canvasBg from '../assets/canvas-bg.png'
import fengImg from '../assets/canvas-feng.png'
import shuiImg from '../assets/canvas-shui.png'
import guangImg from '../assets/canvas-guang.png'
import avater1 from '../assets/type-1.png'
import avater2 from '../assets/type-2.png'
import avater3 from '../assets/type-3.png'
import dayjs from 'dayjs'
import { Indicator } from 'mint-ui'

export default {
  name: 'Home',
  components: {
    PageSecond
  },
  created () {
    // jsBridge.NativeNetworkRequest({
    //   url: 'internal.validationTokens',
    //   method: 'post',
    //   params: {
    //     token: 'c:app:67F1510767DC46CDB8DCAF6ABD99856A',
    //     greenElectricCode: '11111111111'
    //   }
    // })
  },
  mounted () {
    // 数据
    jsBridge.SetTitleBar({
      isShow: false
    })
    // console.log(parse(location.search.replace('?', '')))
    const code = parse(location.search.replace('?', '')).code
    jsBridge.getToken((res) => {
      // console.log('getToken: ---> ' + res)
      jsBridge.NativeNetworkRequest({
        ua: 'E卡',
        url: 'internal.validationTokens',
        encryption: '1',
        method: 'post',
        params: `{"token":"${res}","greenElectricCode":"${code}"}`
      }, (res) => {
        console.log(res);
        const result = JSON.parse(res || "{}")
        if (result && result.data && result.data.code === 1) {
          this.setInfo(result.data.data || {}, code)
          this.$data.showShareBtn = true
        }
        // console.log('NativeNetworkRequest: ---> ', JSON.stringify(res))
      })
    })
    
    // const cvs = document.getElementById('cvs')
    // const ctx = cvs.getContext('2d')
    // this.$data.ctx = ctx
    // this.createCanvas()

    // schema 打开
    if (!this.isWeixin) {
      const { origin, host, pathname, search } = location
      const queryParams = parse(search.replace('?', ''))
      const { goDownloadPage, code } = queryParams
      // console.log(`echargeapp://${host}${pathname}?code=${code}`)
      if (goDownloadPage) {
        location.href = `echargeapp:///apptab/selected?index=0`
        // console.log(`echargeapp://${host}${pathname}?code=${code}`)
        setTimeout(() => {
          location.href = 'http://www.echargenet.com/portal/csService/html/app.html'
        }, 500)
      }
    }
  },
  methods: {
    // 画布
    createCanvas () {
      Indicator.open()
      const { ctx } = this
      const { drawFont } = this

      const { info } = this.$data
      
      // 背景
      const bg = new Image()
      bg.src = canvasBg
      bg.onload = () => {
        ctx.drawImage(bg, 0, 0, 375, 667);

        // 电类型
        const typeMap = {
          '风电': fengImg,
          '光电': guangImg,
          '水电': shuiImg
        };
        const typeImg = new Image();
        typeImg.src = typeMap[info.typeName] || fengImg;
        console.log(typeMap[info.typeName] || fengImg)
        const positionX = 118;
        const positionY = 54.16;
        typeImg.onload = () => {
          setTimeout(() => {
            ctx.drawImage(typeImg, positionX, positionY, 140, 133);
          }, 100)
        }

        // from
        drawFont({
          text: info.genProvince,
          x: 56.77,
          y: 266.49,
          fontSize: 16,
          color: '#F7F4EC'
        })
        // to
        drawFont({
          text: info.receiveProvince,
          x: 267.9,
          y: 266.49,
          fontSize: 16,
          color: '#F7F4EC'
        })

        // 绿电通证
        drawFont({
          text: info.miniId,
          x: 74,
          y: 331,
          fontSize: 14,
          color: '#333333'
        })

        // 类型
        drawFont({
          text: info.typeName,
          x: 162,
          y: 371,
          fontSize: 12,
          color: '#333333'
        })

        // 哈希
        drawFont({
          text: info.contractTxHashFormat,
          x: 162,
          y: 391,
          fontSize: 12,
          color: '#333333'
        })

        // 时间
        drawFont({
          text: info.certificateCreateTimeFormat,
          x: 162,
          y: 413,
          fontSize: 12,
          color: '#333333'
        })

        // 编号
        drawFont({
          text: info.operatorId,
          x: 162,
          y: 434,
          fontSize: 12,
          color: '#333333'
        })

        // 名称
        drawFont({
          text: info.operatorName,
          x: 162,
          y: 454,
          fontSize: 12,
          color: '#333333'
        })

        
        // 二维码
        const codeWrapper = document.getElementById("qrcode")
        const { host, pathname, search } = window.location
        let codeText = `http://${host}${pathname}?goDownloadPage=1`
        // 二维码在192-220区间没办法支持，规避做法
        if (codeText.length >= 192 || codeText.length <= 220) {
          codeText += '&useless=nnnnnnnnnnnnnnnnnnn'
        }
        new QRCode(codeWrapper, {
          text: codeText,
          // text: 'http://evone-test.sodoulala.com/dist/index.html?code=421220ac9bb83094b8bafcae2f00012851c0df23bad19ddcf86339ad371e752de1690x4ed2a61c500e0d70e3937d7d5a716a8118e3d684aee0ef5abed1bd0c71c9c0871&no=nnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnnn',
          width: 112,
          height: 112,
          colorDark : "#4A8A68",
          colorLight : "#ffffff",
          correctLevel : QRCode.CorrectLevel.H
        })
        setTimeout(() => {
          // this.$data.showShareBtn = true
          ctx.drawImage(codeWrapper.getElementsByTagName('img')[0], 132, 484, 112, 112)
          // console.log(base64Str)
          const canvas = document.getElementById('cvs')
          // console.log(canvas.toDataURL('image/png', 0.7))
          Indicator.close();
          jsBridge.ShareEvent({
            title: 'test',
            url: 'test',
            imageUrl: canvas.toDataURL('image/png', 0.7),
            // imageData: base64Str,
            // imageUrl: base64Str,
            type: 2
          })
        }, 200)
      }
    },
    showPopup () {
      this.visible = true
    },
    showPopup2 () {
      this.visible2 = true
    },
    // 文字
    drawFont ({ x, y, fontSize, color, text }) {
      const { ctx } = this
      ctx.fillStyle = color
      ctx.font = `${fontSize}px  Noto Sans SC`
      ctx.textAlign = 'left'
      ctx.textBaseline = 'top'
      ctx.fillText(text, x, y)
    },
    back () {
      jsBridge.turnback()
    },
    share () {

      const cvs = document.getElementById('cvs')
      const ctx = cvs.getContext('2d')
      this.$data.ctx = ctx
      this.createCanvas()
      // html2canvas(document.getElementById('export')).then(function (canvas) {
      //   jsBridge.ShareEvent({
      //     title: 'test',
      //     url: 'test',
      //     // imageData: canvas.toDataURL('image/png', 1).replace('data:image/png;base64,', ''),
      //     imageData: base64Str,
      //     type: 2
      //   })
      // })
    },
    setInfo (info, code) {
      let id = ''
      let contractTxHashFormat = ''
      let certificateCreateTimeFormat = ''
      if (typeof info.contractTxHash === 'string') {
        contractTxHashFormat = `${info.contractTxHash.slice(-8)}******${info.contractTxHash.slice(0, 8)}`
      }

      if (typeof code === 'string') {
        id = `${code.slice(-10)}******${code.slice(0, 10)}`
      }

      if (info.certificateCreateTime) {
        certificateCreateTimeFormat = dayjs(info.certificateCreateTime).format('YYYY年MM月DD日 HH:mm:ss')
      }
      
      this.$data.info = {
        ...info,
        id: code,
        miniId: id,
        contractTxHashFormat,
        certificateCreateTimeFormat
      }
    }
  },
  data: () => {
    return {
      ctx: null,
      options: {
      },
      visible: false,
      visible2: false,
      info: {
        // genProvince: 'test'
        // typeName: '水电'
      },
      isWeixin: isWeixin(),
      href: window.location.href,
      avaterMap: {
        1: avater1,
        2: avater2,
        3: avater3,
      },
      showShareBtn: false
    }
  }
}
</script>
